<template>
  <div>
    <h2></h2>

    <el-row>
      <el-button type="warning" @click="dialogVisible = true; needadd(); "></el-button>

      <el-dialog title="" :visible.sync="dialogVisible" width="25%">
        <el-form ref="form" :model="form" label-width="60px">
          <el-form-item label="">
            <el-input v-model="form.thetime"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.ctitle"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.content"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false ;sumbitaddRow();"> </el-button>
      </span>
      </el-dialog>


      <el-button type="success" class="el-button-fabu"  @click="open"></el-button>
    </el-row>

    <el-table
        border
        :data="tableData"
        style="width: 100%">
      <el-table-column
          type="index"
          label=" "
          width="50">
      </el-table-column>
      <el-table-column
          label=""
          prop="thetime"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="ctitle"
          width="200">
      </el-table-column>
      <el-table-column
          label=""
          prop="content">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="info"
              @click="handleEdit(scope.$index, scope.row)"></el-button>

          <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :visible.sync="centerDialogVisible">
      <el-form ref="form" :model="editForm" label-width="60px">
        <el-form-item label="">
          <el-input v-model="editForm.thetime"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.ctitle"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.content"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button @click="centerDialogVisible=false"></el-button>
        <el-button type="primary" @click="sumbitEditRow()"></el-button>
      </div>
    </el-dialog>
    <el-button-group>
      <el-button type="primary" icon="el-icon-arrow-left"  size="mini"></el-button>
      <el-button type="primary"  size="mini"><i class="el-icon-arrow-right el-icon--right" ></i></el-button>
    </el-button-group>
  </div>
</template>
<script>
let _index;
let a_index;
export default {
  name: "companyevents",
  data() {
    return {
      dialogVisible:false,
      centerDialogVisible:false,
      form:[],
      editForm:[],
      tableData: [{
        thetime: '2020-12-30',
        ctitle: 'vue',
        content: 'vue',
      }, {
        thetime: '2020-12-30',
        ctitle: 'Java',
        content: 'Java',
      }, {
        thetime: '2020-12-30',
        ctitle: 'python',
        content: 'python',
      }, {
        thetime: '2020-12-30',
        ctitle: 'c',
        content: 'c',
      }],
    }
  },
  methods: {
    handleEdit(index, row) {
      // console.log(index, row);
      this.centerDialogVisible = true
      this.editForm = Object.assign({}, row);//
      _index = index;
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    needadd(){
      this.tableData.push(this.form);
    },
    sumbitEditRow() {
      const editData = _index;
      console.log("editData"+	this.editForm)
      this.tableData[editData].thetime = this.editForm.thetime;
      this.tableData[editData].ctitle = this.editForm.ctitle;
      this.tableData[editData].content = this.editForm.content;
      this.centerDialogVisible = false;
      console.log(""+this.editForm.date)
      console.log("",this.tableData)
    },
    sumbitaddRow() {
      const addData = a_index;
      console.log("addData"+	this.Form)
      this.tableData[addData].thetime = this.Form.thetime;
      this.tableData[addData].ctitle = this.Form.ctitle;
      this.tableData[addData].content = this.Form.content;
      this.dialogVisible = false;
      console.log(""+this.Form.date)
      console.log("",this.tableData)
    },
    open() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<style scoped>
.el-button-group{
  margin-top: 15px;
}
.el-table{
  margin-top: 15px;
}
.el-button-fabu{
  margin-left: 15px;
}
</style>