<template>

    <div class="container">
        <h1></h1>
        <el-button type="success" plain @click="handleAddClick"></el-button>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="number"
                    label=""
                    width="60">
            </el-table-column>
             <el-table-column
                    prop="date"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="title"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="messages"
                    width="400"
                    label="">
            </el-table-column>


            <el-table-column label="" width="177">
                <template slot-scope="scope">
                    <el-button type="primary" @click="handleEditClick(scope.$index,scope.row)"  size="mini"></el-button>
                    <el-button type="danger" size="mini" @click="handleDelClick(scope.$index,scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" :visible.sync="editBox" width="50%" :before-close="handleClose">
            <el-form ref="form" label-width="100px" v-model="user">
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "user.number"></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "user.date"></el-input>
                </el-form-item>
                <!--                <el-form-item label=":">-->
                <!--                    <el-date-picker-->
                <!--                            v-model = "user.date"-->
                <!--                            type="date"-->
                <!--                            placeholder="">-->
                <!--                    </el-date-picker>-->
                <!--                </el-form-item>-->
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "user.title"></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "user.messages"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleEditUser"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="addBox" width="50%" :before-close="handleClose">
            <el-form ref="form" label-width="100px" v-model="addUserData">
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "addUserData.number"></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-date-picker
                            v-model = "addUserData.date"
                            type="date"
                            placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "addUserData.title"></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input placeholder="" maxlength="50" v-model = "addUserData.messages"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleAddUser"> </el-button>
            </span>
        </el-dialog>

    </div>
</template>


<script>
    export default {
        name: "addQiye",
        data() {
            return {
                tableData: [{
                    number:'1',
                    date: '2016-03-02',
                    title: '',
                    messages: ' 1518 '
                }, {
                  number:'',
                    date: '2016-03-02',
                    title: '',
                    messages: ' 1518 '
                }, {
                    number:'',
                    date: '2016-03-02',
                    title: '',
                    messages: ' 1518 '
                }, {
                    number:'',
                    date: '2016-03-02',
                    title: '',
                    messages: ' 1518 '
                },{
                  number:'',
                  date: '2016-03-02',
                  title: '',
                  messages: ' 1518 '
                },{
                  number:'',
                  date: '2016-03-02',
                  title: '',
                  messages: ' 1518 '
                }],
                addBox : false,
                editBox:false,
                user:{},
                editIndex:"",
                addUserData:{
                    number:'',
                    date : "",
                    title : "",
                    message : ""
                }
            }
        },
        methods:{
            handleEditClick(index,row){
                this.editBox = true
                this.user = row
                this.editIndex = index
            },

            // eslint-disable-next-line no-unused-vars
            handleDelClick(index,row){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.tableData.splice(index,1)
                    this.$message({showClose: true, message: "", type: 'success'});
                })
            },
            handleClose(done) {
                done();
            },
            handleEditUser(){
                this.tableData.splice(this.editIndex,1,this.user)
                this.$message({showClose: true, message: "", type: 'success'});
                this.editBox = false
            },
            handleAddClick(){
                this.addBox = true
            },
            handleAddUser(){
                let strDate = dateFormat("YYYY-mm-dd",this.addUserData.date)
                this.addUserData.date = strDate
                this.tableData.push(this.addUserData)
                this.addBox = false
                this.addUserData = {}
                this.$message({showClose: true, message: "", type: 'success'});
            }
        }
    }

    function dateFormat(fmt, date) {
        let ret;
        const opt = {
            "Y+": date.getFullYear().toString(),        // 
            "m+": (date.getMonth() + 1).toString(),     // 
            "d+": date.getDate().toString(),            // 
            "H+": date.getHours().toString(),           // 
            "M+": date.getMinutes().toString(),         // 
            "S+": date.getSeconds().toString()          // 
            // 
        };
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
            }
        }
        return fmt;

    }
</script>

