<template>
    <Table border :columns="columns12" :data="data6">
        <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{row,index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)"></Button>
            <Button type="error" size="small" @click="remove(index)"></Button>
        </template>
    </Table>
</template>
<script>
    export default {
        data () {
            return {
                columns12: [
                    {
                        title: '',
                        slot: 'name'
                    },
                    {
                        title: '',
                        key: 'sex'
                    },
                    {
                        title: '',
                        key: 'address'
                    },
                    {
                        title: '',
                        key: 'school'
                    },
                    {
                        title: '',
                        key: 'exp'
                    },
                    {
                        title: 'Action',
                        slot: 'action',
                        width: 150,
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'time'
                    },
                ],
                data6: [
                    {
                        name: 'John Brown',
                        sex:'',
                        address: '1999.11',
                        school:'',
                        exp:'3',
                        time:'2016.01.15 16:35',
                        skil:'java'
                    },
                    {
                        name: 'Jim Green',
                        sex: '',
                        address: '1999.4',
                        school:'',
                        exp:'3',
                        time:'2016.01.15 16:35',
                        skil:'web'
                    },
                    {
                        name: 'Joe Black',
                        sex: '',
                        address: '2000.12',
                        school:'',
                        exp:'3',
                        time:'2016.01.15 16:35',
                        skil:'HTML5'
                    },
                    {
                        name: 'Jon Snow',
                        sex: '',
                        address: '2000.12',
                        school:'',
                        exp:'3',
                        time:'2016.01.15 16:35',
                        skil:'JAVA'
                    }
                ],
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: '',
                    content: `${this.data6[index].name}<br>${this.data6[index].sex}<br>${this.data6[index].address}<br>${this.data6[index].school}<br>${this.data6[index].exp}<br>${this.data6[index].skil}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }
        }
    }
</script>