<template>
  <div>
    <h2></h2>

    <el-row :gutter="30">

      <el-col :span="8" >
        <div>
          <el-input placeholder="" clearable v-model="search" >
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
      </el-col>

      <el-col :span="16"  >

        <div>
          <el-button type="primary" @click="dialogVisible=true; needadd();"></el-button>

          <el-dialog title="" :visible.sync="dialogVisible" width="30%">
            <el-form ref="form" :model="form" label-width="100px" :label-position="right">
              <el-form-item label="">
                <el-input v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.order"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.tel"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.role"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.post"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.department"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.leader"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.area"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.description"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false;sumbitaddRow();"> </el-button>
      </span>
          </el-dialog>
        </div>

      </el-col>

    </el-row>

    <el-table
        :data="tables"
        style="width: 100%"
        class="el-table-re">

      <el-table-column
          type="index"
          label=" "
          width="50">
      </el-table-column>

      <el-table-column
          label=" "
          width="120">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.state">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
          label=""
          prop="order"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="name"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="tel"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="role"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="post"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="department"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="start"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="leader"
          width="120">
      </el-table-column>
      <el-table-column
          label=""
          prop="description">
      </el-table-column>
      <el-table-column
          label=""
          prop="area"
          width="120">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="centerDialogVisible">

      <el-form ref="form" :model="editForm" label-width="100px">
        <el-form-item label="">
          <el-input v-model="editForm.order"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.role"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.post"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.department"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.leader"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.description"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.area"></el-input>
        </el-form-item>
      </el-form>

      <div>
        <el-button @click="centerDialogVisible=false"></el-button>
        <el-button type="primary" @click="sumbitEditRow()"></el-button>
      </div>
    </el-dialog>

    <el-button-group>
      <el-button type="primary" icon="el-icon-arrow-left"  size="mini"></el-button>
      <el-button type="primary"  size="mini"><i class="el-icon-arrow-right el-icon--right" ></i></el-button>
    </el-button-group>

  </div>
</template>

<script>
let _index;
let a_index;
export default {
  name: "recruitment",
  data() {
    return {
      editForm:[],
      tableData: [{
        state:true,
        order:'1601150930',
        name: '',
        tel:'13833725790',
        role:'',
        post:'java',
        department:'',
        start:'2020-12-30',
        leader:'',
        description:'',
        area:'java',
      }, {
        state:false,
        order:'1601150930',
        name: '',
        tel:'13833725790',
        role:'',
        post:'java',
        department:'',
        start:'2020-12-30',
        leader:'',
        description:'',
        area:'java',
      }, {
        state:false,
        order:'1601150930',
        name: '',
        tel:'13833725790',
        role:'',
        post:'java',
        department:'',
        start:'2020-12-30',
        leader:'',
        description:'',
        area:'java',
      }, {
        state:true,
        order:'1601150930',
        name: '',
        tel:'13833725790',
        role:'',
        post:'java',
        department:'',
        start:'2020-12-30',
        leader:'',
        description:'',
        area:'java',
      }],
      dialogVisible:false,
      centerDialogVisible:false,
      form: {
        state:true,
        order:'',
        name: '',
        leader:'',
        description:'',
        department:'',
        start:(new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate()),
        role:'',
        post:'',
        area:'',
      },
      search: '',
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(data => {
          return Object.keys(data).some(key => {
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  },
  methods: {
    handleEdit(index, row) {
      // console.log(index, row);
      this.centerDialogVisible = true
      this.editForm = Object.assign({}, row);//
      _index = index;
    },
    needadd(){
      this.tableData.push(this.form);
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    sumbitEditRow() {
      const editData = _index;
      console.log("editData"+	this.editForm)
      this.tableData[editData].order = this.editForm.order;
      this.tableData[editData].name = this.editForm.name;
      this.tableData[editData].tel = this.editForm.tel;
      this.tableData[editData].role = this.editForm.role;
      this.tableData[editData].post = this.editForm.post;
      this.tableData[editData].department = this.editForm.department;
      this.tableData[editData].leader = this.editForm.leader;
      this.tableData[editData].description = this.editForm.description;
      this.tableData[editData].area = this.editForm.area;
      this.centerDialogVisible = false;
      console.log(""+this.editForm.date)
      console.log("",this.tableData)
    },
    sumbitaddRow() {
      const addData = a_index;
      console.log("addData"+	this.Form)
      this.tableData[addData].name = this.Form.name;
      this.tableData[addData].order = this.Form.order;
      this.tableData[addData].tel = this.Form.tel;
      this.tableData[addData].department = this.Form.department;
      this.tableData[addData].role = this.Form.role;
      this.tableData[addData].post = this.Form.post;
      this.tableData[addData].area = this.Form.area;
      this.tableData[addData].leader = this.Form.leader;
      this.tableData[addData].description = this.Form.description;
      this.dialogVisible = false;
      console.log(""+this.Form.date)
      console.log("",this.tableData)
    },
  }
}

</script>

<style scoped>
.el-row{
  margin-top: 15px;
}
.el-table-re{
  margin-top: 15px;
}
.el-button-group{
  margin-top: 15px;
}
</style>