<template>
<div class="change">
<ul>
  <li v-for="(item,index) in list" :key="item" @click="active(index)" :class="{word:index== isactive}" class="first">
    <p>{{item}}</p>
    <div v-show="index== isactive" class="bot">
      <ul>
        <li v-for="items in list1[index]" :key="items" class="two">{{items}}</li>
      </ul>
    </div>
  </li>
</ul>
</div>
</template>

<script>
export default {
  name: "change",
  data(){
    return{
      list:['','','','',''],
      list1:[
        ['','','','',' ',' ',' ',' ',' ',' ',' ',' '],
        ['',' ',' ',' ',' ',' ',' ','',' ',' ',' shopee'],
        ['','  ',' ','',' ',' '],
        ['',' ','  ','',' ',' ',' shopee'],
        ['',' ',' ',' ',' ',' ',' ']],
      isactive:0
    }
  },
  methods:{
    active(num){
    this.isactive = num
    }

  }
}
</script>

<style scoped>
.change{
  width: 1200px;
  margin: 50px auto;
  height: 140px;
}
.first{
  width: 100px;
  float: left;
  font-size: 18px;
  margin: 10px 10px 0 0;
}
.word p{
  border-bottom: 2px solid gray;
  width: 100px;
}
.first p:hover{
  cursor: pointer;
  color: #42b983;
}
ul:nth-of-type(1){
  position: relative;

}
.bot{
  width: 1200px;
  height: 50px;
  position: absolute;
  left: 10px;
  text-align: left;
  margin-top: 15px;
  font-size: 15px;
  color: gray;
  top: 70px;
}
li{
  list-style: none;
  float: left;
}
.two{
  margin: 0 20px 10px 0;
}
.two:hover{
  color: #42b983;
  cursor: pointer;
}
</style>