      }]
    }
  },
   methods:{
        isChoose(index){
          if(index==1){
              if(this.isChecked==1){
                  //
                  this.isChecked=0;
              }else {
                  this.isChecked=1;
              }
          }else {
              if(this.isChecked==-1){
                  this.isChecked=0;
              }else {
                  this.isChecked=-1;
              }
          }
        }
    },

  computed: {
// 
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(data => {
          return Object.keys(data).some(key => {
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  }
}
